var disbled = false;
export default {
	data() {
		return {
			roll: [],
			name: '',
			phonenum: '',
			btnShow: false,
			popShow: false
		};
	},
	onLoad() {
		this.rollData();
	},
	onPageScroll(e) {
		if (e.scrollTop > 550) {
			this.btnShow = true;
			return;
		}
		if (e.scrollTop < 550) {
			this.btnShow = false;
			return;
		}
	},
	methods: {
		getPhoneNumberHandler (e) {
			console.log(e.detail.errMsg);
			console.log(e.detail.iv);
			console.log(e.detail.encryptedData);
		},
		inputName(e) {
			console.log(e);
			this.name = e.detail.value;
		},
		inputPhone(e) {
			this.phonenum = e.detail.value;
		},
		postForm() {
			if (disbled) return;
			var phone = this.checkPhoneNum(this.phonenum);
			var name = this.name.trim();
			console.log(name);
			uni.pageScrollTo({
				scrollTop: 0
			});
			if (name == '') {
				uni.showModal({
					title: '提示',
					content: '请输入宝贝姓名',
					showCancel: false
				});
				return;
			}
			if (this.phonenum == '') {
				uni.showModal({
					title: '提示',
					content: '请输入手机号',
					showCancel: false
				});
				return;
			}
			console.log(phone);
			if (phone) {
				disbled = true;
				this.collectphonenumsApi().then(res => {
					if (res.data.errorCode == 0) {
						this.popShow = true;
						disbled = false;
					}
				});
			} else {
				uni.showModal({
					title: '提示',
					content: '手机号格式错误，请重新输入',
					showCancel: false
				});
			}
		},
		//验证手机号
		checkPhoneNum(phoneNumber) {
			let str = /^1[0-9]{10}$/;
			if (str.test(phoneNumber)) {
				return true;
			} else {
				return false;
			}
		},
		collectphonenumsApi() {
			return new Promise((resolev, reject) => {
				uni.request({
					url: 'https://online.xiongmaolaoshi.com/goods/collectphonenums',
					//url: 'https://pandatest.junxing-tech.com/goods/collectphonenums',
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					method: 'POST',
					data: {
						name: this.name,
						phonenum: this.phonenum,
						type: 'douyin_xcx'
					},
					success: resolev,
					fail: reject
				});
			});
		},
		pageScroll() {
			uni.pageScrollTo({
				scrollTop: 0
			});
		},
		//生成滚动数据
		rollData() {
			let _this = this;
			for (let index = 0; index < 20; index++) {
